.main-btn {
  position: relative;
  display: block;
  width: 50px;
  height: 50px;
  margin-left: 25px;
  color: #ffffff;
  background-color: #ff4081;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  -webkit-transition: background-color 0.3s ease-in 0s;
  -o-transition: background-color 0.3s ease-in 0s;
  transition: background-color 0.3s ease-in 0s; }
  .main-btn:hover {
    background-color: #e74c3c; }
  .main-btn:after {
    content: "+";
    position: absolute;
    top: 5px;
    left: 13px;
    font-size: 37px; }

.btn {
  display: block;
  padding: 14px;
  font-size: 16px;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  background-color: #e74c3c;
  -webkit-transition: background-color 0.5s ease;
  -o-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease; }
  .btn:hover {
    background-color: #e77e2d; }
  .btn--edit {
    width: 100%; }
  .btn--remove {
    width: 100%;
    margin-top: 15px; }
  .btn--cancel {
    margin: 0 25px; }



